<template>
  <div style="position:relative" class="Loginwrapper" v-on:keyup.enter="submitLogin">
    <div class="bgpic"></div>

   

      <transition
        enter-active-class="animated fadeInLeft"
        leave-active-class="animated fadeOutRight"
       >
      >
    <el-form
        v-if="flag"
      :rules="rules"
      ref="loginForm"
      v-loading="loading"
      element-loading-text="..."
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
      :model="loginForm"
      label-width="6rem"
      class="specialELContainer"
    >
      <h3 class="loginTitle"></h3>
      <el-form-item prop="username" label="">
        <el-input
          size="normal"
          type="text"
          v-model="loginForm.username"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="password" label="">
        <el-input
          size="normal"
          type="password"
          v-model="loginForm.password"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>
      <!-- <el-form-item prop="code">
                <el-input size="normal" type="text" v-model="loginForm.code" auto-complete="off"
                          placeholder="" @keydown.enter.native="submitLogin" style="width: 250px"></el-input>
                <img :src="vcUrl" @click="updateVerifyCode" alt="" style="cursor: pointer">
      </el-form-item>-->
      <!-- <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox> -->
      <div class="goRegister" @click="goRegister"></div>
      <div style="width:3rem;height:3rem"></div>
      <div class="topTextButtonBlue"  style="width:6rem;margin-left:40%"  @click="submitLogin" ></div>
    </el-form>
      </transition>

      <transition
        enter-active-class="animated fadeInLeft"
        leave-active-class="animated fadeOutRight"
      >
    <el-form
      v-if="flag2"
      :rules="regirules"
      ref="regiForm"
      v-loading="loading"
      element-loading-text="..."
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(22, 44,22, 0.8)"
      :model="regiForm"
      label-width="6rem"
      class="specialELContainer"
    >
      <h3 class="loginTitle"></h3>
      <el-form-item prop="phone" label="">
        <el-input
          size="normal"
          type="text"
          v-model="regiForm.phone"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>

      <el-form-item prop="username" label="">
        <el-input
          size="normal"
          type="text"
          v-model="regiForm.username"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="password" label="">
        <el-input
          size="normal"
          type="password"
          v-model="regiForm.password"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>

      <el-form-item prop="repassword" label="">
        <el-input
          size="normal"
          type="password"
          v-model="regiForm.repassword"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>

      <el-form-item prop="workplace" label="">
        <el-input
          size="normal"
          type="text"
          v-model="regiForm.workplace"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>

      <!-- <el-form-item prop="code">
                <el-input size="normal" type="text" v-model="loginForm.code" auto-complete="off"
                          placeholder="" @keydown.enter.native="submitLogin" style="width: 250px"></el-input>
                <img :src="vcUrl" @click="updateVerifyCode" alt="" style="cursor: pointer">
      </el-form-item>-->
      <!-- <el-checkbox size="normal" class="loginRemember" v-model="checked"></el-checkbox> -->
      <div class="goRegister" @click="goLogin"></div>
      <div class="topTextButtonBlue"    style="width:6rem;margin-left:40%"   @click="submitRegi"></div>
    </el-form>
      </transition>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Login",
  created(){
                window.scrollTo(0,0)

return
    var k={
        "id":"1",
        "box":"22",
        "tableNumber":"",
        "docCount":"3",
        "from":"1",
        "to":"33",
        "totalPage":"222",
        "remark":"",
        "publisher":"tlq",
        "checker":"chl",
        "publishTime":"2020-02-02",
        "checkTime":"2021-02-02",
        "authName":""
    }
    var arr=[]
    arr.push(k)
    arr.push(k)

    axios({
            method: "post",
            url: "http://localhost:8080/download",
            responseType: "arraybuffer",
            data:arr,
          }).then((res) => {
            // //console.log("DASDASDASDASD")

            //console.log(res);
            const blob = new Blob([res]); //new Blob([res])data[objece objece]

            const fileName = ".zip"; //xlsx-1,xls

            const elink = document.createElement("a");

            elink.download = fileName;

            elink.style.display = "none";

            elink.href = URL.createObjectURL(blob);

            document.body.appendChild(elink);

            elink.click();

            URL.revokeObjectURL(elink.href); // URL 
            document.body.removeChild(elink);
            this.showWaitingFlag = false;
          });


  },
  data() {
              var validatePass = (rule, value, callback) => {
          if (value === '') {
              callback(new Error('1'));
          } else if (value != this.regiForm.password) {
              callback(new Error('!'));
          } else {
              callback();
          }
      };  //

    return {
      flag:true,
      flag2:false,
      loading: false,
      vcUrl: "/verifyCode?time=" + new Date(),
      loginForm: {
        username: "",
        password: ""
      },
      regiForm: {
        username: "",
        password: "",
        phone:"",
        workplace:"",
        repassword:""
      },
      checked: true,
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }]
        // code: [{required: true, message: '', trigger: 'blur'}]
      },
      regirules: {
        username: [
          { required: true, message: "", trigger: "blur" },
         	{validator:function (rule,value,callback) 
{ 
  var fData=value
    var intLength=0 
    for (var i=0;i<fData.length;i++) 
    { 
        if ((fData.charCodeAt(i) < 0) || (fData.charCodeAt(i) > 255)) 
            intLength=intLength+2 
        else 
            intLength=intLength+1    
    } 
    if(intLength<11&&intLength>4){
      callback()
    }else{
       callback(new Error("4-10,6"));
    }
} , trigger: 'blur'}

        ],
        password: [{ required: true, message: "", trigger: "blur" },
        { min: 5,max: 25,message: ' 5  25'},
        ],

        repassword: [{ required: true, message: "", trigger: "blur" },
        {validator: validatePass, trigger: 'blur'},
         { min: 5, max: 16, message: ' 5  16 ', trigger: 'blur' },
 
         ],

        phone: [{ required: true, message: "", trigger: "blur" },
              	{validator:function(rule,value,callback){
	            if(/^1[34578]\d{9}$/.test(value) == false){
	                callback(new Error(""));
	            }else{
	                callback();
	            }
	        }, trigger: 'blur'}

        ],


        workplace: [{ required: true, message: "", trigger: "blur" }],

        // code: [{required: true, message: '', trigger: 'blur'}]
      },
    };

  },
  methods: {
    submitRegi(){
            this.$refs.regiForm.validate(valid => {

              // if(this.regiForm.repassword!=this.regiForm.password){
              //     this.$message({
              //           type: "success",
              //           message: "!"

              //         });
              // }
        if (valid) {
          this.loading = true;
          var regiData=JSON.stringify(this.regiForm)
          console.log(regiData)
          this.postKeyValueRequest("/register", regiData)
            .then(resp => {
              if (resp) {
                console.log(resp)
                if(resp.code==0){
                
                        this.$message({
                        type: "success",
                        message: "!"

                      });

                      this.flag2=false;
                        setTimeout(() => {
                        this.flag=true;
                      }, 850);
                }
                  // this.$store.commit("INIT_CURRENTHR", resp.obj);
                  // window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
                  // let path = this.$route.query.redirect;
                  // console.log(resp.obj);
                  // this.$router.push(
                  //   path == "/" || path == undefined ? "/" : path
                // );
              } else {
                alert('')
              }
            })
            .then(() => {
              this.loading = false;
            });
        } else {
          return false;
        }
      });
    },
    goRegister() {
      this.flag=false;
      setTimeout(() => {
        this.flag2=true;
      }, 850);
    },
    goLogin(){
      this.flag2=false;
      setTimeout(() => {
        this.flag=true;
      }, 850);
    },
    updateVerifyCode() {
      this.vcUrl = "/verifyCode?time=" + new Date();
    },
    
    submitLogin() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true;
          var logindata=JSON.stringify(this.loginForm)
          console.log(logindata)
          this.postKeyValueRequest("/login", logindata)
            .then(resp => {
              if (resp) {
                if(resp.code==1701){
                    this.$message({
                        type: "warning",
                        message: "!"
                      });
                      this.loading=false
                      return
                }
                console.log(resp)
                if(resp.code==0){
                  var thistoken=resp.data.token.split('"')[1];
                  if(thistoken==null) thistoken=resp.data.token; 
                  window.window.localStorage.setItem("token",thistoken)
                  window.sessionStorage.setItem("userId",this.loginForm.username)
                  window.sessionStorage.setItem("expireTime",resp.data.endTime)

                  window.sessionStorage.setItem("userIdNum",resp.data.userId)
                  window.sessionStorage.setItem("admin",'')

                  if(resp.data.role==''){
                  window.sessionStorage.setItem("admin",'1')
                  // window.sessionStorage.setItem("admin",'1')
                  this.$store.state.admin=1

                  }

                            this.$message({
                        type: "success",
                        message: "!"
                      });

                      this.$router.push('/work/newBatch')
                }
                
                // else if(){}
                  // this.$store.commit("INIT_CURRENTHR", resp.obj);
                  // window.sessionStorage.setItem("user", JSON.stringify(resp.obj));
                  // let path = this.$route.query.redirect;
                  // console.log(resp.obj);
                  // this.$router.push(
                  //   path == "/" || path == undefined ? "/" : path
                // );
              } else {
                alert('')
              }
            })
            .then(() => {
              this.loading = false;
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss" >
.specialELContainer {
  .el-input__inner {
    background: rgba(255, 255, 255, 0.15) !important;
    border-radius: 0.6rem;
    border: 0.05rem solid #8992acc5;
    height: 3rem !important;
    color: #222;
    font-size: 1.1rem;
    padding-left: 3.3rem !important;
    width:100%;

      .el-select {
    width: 100%;
  }

  &::-webkit-input-placeholder {
    /* WebKit browsers  */
 color: rgb(155,155,155) !important;
  }

  &:-moz-placeholder {
    /* Mozilla Firefox 4 to 18  */
 color: rgb(155,155,155) !important;
  }

  &::-moz-placeholder {
    /* Mozilla Firefox 19+  */
  color: rgb(155,155,155) !important;
  }

  &:-ms-input-placeholder {
    /* Internet Explorer 10+  ie*/
 color: rgb(155,155,155) !important;
  }
  
  }
  
  
    .el-input__inner:hover{
    border: 0.05rem solid #b8c1dac5;

  }
  input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0px 1000px rgb(203, 231, 215) inset !important;
    -webkit-text-fill-color: #3e3e3e !important;
    font-size: 1.1rem !important;
  }

  .el-form-item__label:before {
    content: "" !important;
  }
  .el-form-item__label {
    font-size:0.9rem !important;
    font-weight: 600;
    line-height: 3rem !important;
  }
  position: relative;
  padding: 15px 35px 15px 35px;
  top: 1rem;
  background: rgba(255, 255, 255, 0) !important;
}
</style>


<style lang="scss" scoped>
.Loginwrapper {
  background-color: rgb(255, 255, 255);

  height: 150vh;

  .specialELContainer {
 
    position: relative;
    border-radius: 15px;
    background-clip: padding-box;
    margin: 0 auto;
    width: 30rem;
    top: 7rem;
    padding: 15px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
}

.Loginwrapper >>> .el-form-item__label {
  // font-size: 6rem !important;
}

.loginBtn {
  margin-top: 3rem !important;
  margin-bottom: 1.6rem !important;

  left: 20%;
  width: 60%;
  height: 2.5rem;
  position: relative;
}

.bgpic {
  /* background: url("../assets/welcome.png"); */
  width: 52rem;
  height: 52rem;
  position: absolute;
  left: -1%;
  top: 3%;
  background-repeat: no-repeat;
  background-size: 100%;
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
  opacity: 0.5;
}
.goRegister {
  position: absolute;
  right: 2rem;
  cursor: pointer;
}
.goRegister:hover {
  color: #566f91;
}

.loginTitle {
  margin: 1rem auto 1.5rem auto;
  text-align: center;
  color: #505458;
}

.loginRemember {
  text-align: left;
  margin: 0px 0px 15px 0px;
}
.el-form-item__content {
  display: flex;
  align-items: center;
}
</style>